.link {
    text-decoration: none;
    color: #313131
}

.movie_details_wrapper {
    display: flex;
    justify-content: center;
    padding: 10px 0;
    gap: 30px;
}

.poster {
    width: 20%;
}

.general {
    width: 80%;
}

.movie_title {
    font-size: 28px;
}

.info {
    font-size: 22px;
}

.text {
    font-size: 18px;
    margin: 0;
    padding: 20px;
}

.list {
    display: flex;
    list-style: none;
    gap: 20px;
    margin: 0;
    padding: 20px;
    font-size: 18px;
}

.list_subcategories {
    padding: 20px 0;
}

.list_subcategories a {
    padding: 5px 15px;
    text-transform: capitalize;
    border-radius: 20px;
    border: 1px solid #313131;
}

.list_subcategories a:hover {
    background-color: #e0e0e0;
    transition: background 0.3s ease, color 0.3s ease;
}

.link_back {
    text-transform: lowercase;
    display: inline-block;
    border-radius: 20px;
    border: 1px solid #312133;
    padding: 8px 12px;
    font-size: 18px;
    margin-bottom: 20px;
}
